@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap&subset=latin-ext);
body {
  font-family: 'Lato', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}
.wrapper {
    text-align: center;
}
.find {    
    font-family: 'Lato', sans-serif;
    font-size: 1rem;
    padding: .5rem .75rem;
    margin: 1rem;
    border: none;
    border-bottom: 1px solid black;
}

.find:focus {
    outline: none;
}
.city {
    text-transform: capitalize;
}

.result {
    margin: 1rem 0;
}

li {
    line-height: 2;
}

.failed {
    font-size: 1.5rem;
}

.icon {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
}
